import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from "vuex-persistedstate";
import Cookies from 'js-cookie'
import dict from "./assets/dic/dic.json";//引入缓存的数据字典json
import { dictionarys, getAllSites, getGroupAll } from "./api/api.js"
Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        token: null,
        tokenExpire: null,
        tagsStoreList: [],
        language: Cookies.get('language') || 'en',
        //数据字典
        options: dict
    },
    getters: {
        tagsStoreList: state => state.tagsStoreList
    },
    plugins: [
        createPersistedState({
            storage: window.sessionStorage,
            reducer(val) {
                return {
                    // 只储存state中的
                    options: val.options
                };
            }
        })
    ],
    mutations: {
        saveToken(state, data) {
            state.token = data;
            window.sessionStorage.setItem("Token", data);
        },
        saveTokenExpire(state, data) {
            state.tokenExpire = data;
            window.sessionStorage.setItem("TokenExpire", data);
        },
        saveTagsData(state, data) {
            state.tagsStoreList = data;
            sessionStorage.setItem("Tags", data)
        },
        SET_LANGUAGE: (state, language) => {
            state.language = language
            Cookies.set('language', language)
        },
        setOptions(state, obj) {
            let { key, value } = obj;
            try {
                state.options[key] = value;
            } catch (error) {
                console.log(error);
            }
        }
    },
    actions: {
        setLanguage({ commit }, language) {
            commit('SET_LANGUAGE', language)
        },
        /**
                * action函数可以接收一个与store实例具有相同方法的属性context
                 *   context:{    //{Object} store对象  更改 Vuex 的 store 中的状态的唯一方法是提交 mutation
                 *       state,   等同于store.$state，若在模块中则为局部状态
                 *       rootState,   等同于store.$state,只存在模块中
                 *       commit,   等同于store.$commit,必须在mutations中同步操作
                 *       dispatch,   等同于store.$dispatch,dispatch：可以异步操作，例如向后台提交数据
                 *       getters   等同于store.$getters
                 *    }
                *  args   // Array 需要请求的字典keys集合
                *  new Promise() 内的参数是函数,可以将异步方法按一定的顺序执行 ,即（resolve,reject）=> {     } ;
                *  有了Promise对象，就可以将异步操作以同步操作的流程表达出来，避免了层层嵌套的回调函数
                * resolve =》 是个函数，异步成功时调用，并将异步操作的结果返回出去;reject  =》 是个函数，异步失败时调用
                */
        getOptions(context, args) {
            return new Promise((resolve, reject) => {
                let dict = context.state.options;//数据字典JSON
                let params = []
                if (args && args.length > 0) {
                    params = args.reduce((o, v, i) => {
                        if (dict[v] && dict[v].length == 0) {   //当缓存中没有时添加到请求中
                            o.push(v);
                        }
                        return o;
                    }, []);
                } else {
                    for (var d in dict) {
                        if (d == "Sites" || d == "Groups") {

                        } else {
                            params.push(d)
                        }
                    }
                }
                if (params.length === 0) {    //当缓存中存在时从缓存中拿数据
                    resolve(true);
                    return false;
                }
                dictionarys()//请求新数据字典存入vuex中
                    .then(
                        res => {
                            if (res.success) {
                                params.map(v => {
                                    context.commit("setOptions", {
                                        key: v,
                                        value: res.data.filter((a) => { return a.datatype == v }) || []
                                    });
                                });
                                resolve(true, res);//第一个参数告诉调用处，数据字典请求成功
                            } else {

                            }

                        },
                        err => {
                            reject(err);
                        }
                    )
                    .catch(err => {
                        reject(err);
                    }
                    )
                getAllSites()//请求新数据字典存入vuex中
                    .then(
                        res => {
                            if (res.success) {
                                context.commit("setOptions", {
                                    key: "Sites",
                                    value: res.data || []
                                });
                                resolve(true, res);//第一个参数告诉调用处，数据字典请求成功
                            } else {

                            }

                        },
                        err => {
                            reject(err);
                        }
                    )
                    .catch(err => {
                        reject(err);
                    }
                    )
                getGroupAll()//请求新数据字典存入vuex中
                    .then(
                        res => {
                            if (res.success) {
                                context.commit("setOptions", {
                                    key: "Groups",
                                    value: res.data || []
                                });
                                resolve(true, res);//第一个参数告诉调用处，数据字典请求成功
                            } else {

                            }

                        },
                        err => {
                            reject(err);
                        }
                    )
                    .catch(err => {
                        reject(err);
                    }
                    )
            });
        }
    }
})



